/**
 * Enum representing different read channel ids.
 */
export enum ChannelId {
    READ_CHANNEL_1 = 0xffffffff,
    READ_CHANNEL_2 = 0xfffffffe,
    READ_CHANNEL_3 = 0xfffffffd,
    READ_CHANNEL_4 = 0xfffffffc,
    READ_CHANNEL_5 = 0xfffffffb,
    READ_CHANNEL_6 = 0xfffffffa,
    READ_CHANNEL_7 = 0xfffffff9,
    READ_CHANNEL_8 = 0xfffffff8,
    READ_CHANNEL_9 = 0xfffffff7,
    READ_CHANNEL_10 = 0xfffffff6,
}
